





























export default {
	data() {
		return {
			name:'',
			id:'',
			zheng:'',
			fan:'',
			zId:'',
			fId:''
		};
	},
	methods:{
		getInfo(e,str){
			if(str == 'name'){
				this.name = e.detail.value
			}else if(str == 'id'){
				this.id = e.detail.value
			}
		},
		submit(){
			var that = this;
			if(that.name == ''){
				uni.showToast({
					title:'请先输入姓名',
					icon:'none'
				})
			}else if(!(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(that.id))){
				uni.showToast({
					title:'身份证号格式不正确!',
					icon:'none'
				})
			}else if(that.zheng=='' || that.fan==''){
				uni.showToast({
					title:'请补全身份证照片!',
					icon:'none'
				})
			}else{
				uni.request({
					url:that.http+'index/setIdcard',
					header:that.header,
					method:'POST',
					data:{
						user_id:uni.getStorageSync('token'),
						user_name:that.name,
						id_card:that.id,
						id_photo:that.zId,
						id_photof:that.fId
					},
					success: (res) => {
						console.log(JSON.stringify(res.data))
						if(res.data.code == 200){
							uni.navigateTo({
								url:'../rnResault/rnResault'
							})
						}else{
							uni.showToast({
								title:res.data.data,
								icon:'none'
							})
						}
					},
					fail(){
						uni.showToast({
							title:'网络延迟，请重试!',
							icon:'none'
						})
					}
				})
			}
		},
		// 上传图片
		uploadImg(str){
			var that = this;
			// 选择图片
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album'],
				success: (wj) => {
					uni.showLoading({
						title: '上传中...'
					})
					// 原生压缩
					plus.zip.compressImage({
							src: wj.tempFilePaths[0],
							dst: "_doc/a.jpg",
							overwrite: true,
							width: '270px',
							height: '270px',
							format: 'jpg',
							quality: 100
						},
						function(e) {
							var reader = new plus.io.FileReader();
							reader.onloadend = function(e) {
								var speech = e.target.result; //base64图片 
								// 上传头像
								uni.request({
									url: that.http + 'index/imgupload',
									header: that.header,
									method: 'POST',
									data: {
										file: speech
									},
									success: (res) => {
										uni.hideLoading()
										if(res.data.code==200){
											if(str == 'zheng'){
												that.zheng = res.data.path;
												that.zId = res.data.id
												console.log(that.zId,that.zheng)
											}else if(str == 'fan'){
												that.fan = res.data.path;
												that.fId = res.data.id
												console.log(that.fId,that.fan)
											}
										}else{
											uni.showToast({
												title:res.data.info,
												icon:'none'
											})
										}
									},
									fail() {
										uni.showToast({
											title: '上传时网络延迟，请刷新后重试!',
											icon: 'none'
										})
									}
								})
							};
							reader.readAsDataURL(e.target);
						},
						function(err) {
							plus.nativeUI.alert('未知错误！', function() {});
						}
					);
				},
				fail() {
				}
			})
		}
	}
}
